nav{
  width: auto;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* transition: 0.1s ease; */
}

ul{
  display: flex;
  align-items: center; 
   justify-content: center;
   margin-bottom: 0;
   font-size: 2.5vw;
   
}
ul li{
  list-style: none;
  margin: 0 1vw;
}
input{
  margin: 0 2vw;
  padding: 1vw;
}
label{
  font-size: 2.5vw;
}
.rightNav{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10vw;
}
.form-switch{
  display: flex;
  align-items: center;
}
#toggleSwitch{
  width: 5vw;
}
.form-check-input:checked {
  background-color: #5c5656;
  border-color: #0010f7;
  width: 5vw;

}

.form-check-input {
  width: 5vw;

  background-color: #ffffff;
  border-color: #000000;
}

.form-check-input:focus {
  width: 5vw;

}
#toUpCaseBtn{
  font-size: 1vw;
font-weight: 700;
  background-color: rgb(0, 0, 246);
  border: none;
  color: black;
  border: none;
  padding: 1vw;
  border-radius: 5vw;
}
#toUpCaseBtn:hover{
  background-color: rgba(0, 0, 246, 0.85);
color: rgba(0, 0, 0, 0.792);
transition: 0.2s ease-in-out;

}
#h1, h5{

  font-size: 2vw;

}
#exampleFormControlTextarea1{
font-size: 2vw;
}

.show{
  font-size: xx-large;
  display: none;
  transition: 0.2s ease;
}
.hideShow{
display: block;
}
